$(document).ready(function() {
    const linkedInCard = new Swiper(".js-linkedInCards-slider", {
        slidesPerView: 1.2,

        spaceBetween: 12,

        // loop: true,
        // autoplay: {
        //     delay: 5000,
        //     speed: 3000,
        //     disableOnInteraction: false
        // },
        breakpoints: {
            640: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            768: {
                slidesPerView: 1.7,
                spaceBetween: 20,
            },
            1024: {
                slidesPerView: 3,

            },
        },

        navigation: {
            nextEl: ".js-arrow-left",
            prevEl: ".js-arrow-right",
        },
    });


    const animationRemote1 = bodymovin.loadAnimation({
        container: document.getElementById('svg__01'),
        // path: 'https://assets9.lottiefiles.com/packages/lf20_hKebN8.json',
        autoplay: true,
        renderer: 'svg',
        loop: true
    })
    const animationRemote2 = bodymovin.loadAnimation({
        container: document.getElementById('svg__02'),
        // path: 'https://assets9.lottiefiles.com/packages/lf20_hKebN8.json',
        autoplay: true,
        renderer: 'svg',
        loop: true
    })





})